<template>
  <div class="download-lead">
    <!-- <pem-scroll-view ref="scroll" class="scroll-view"> -->
      <div class="download-wrapper">
        <div class="logo"></div>
        <div class="intro"></div>
        <div class="btns-wrapper">
          <div class="open-btn" @click="openNow"></div>
          <div class="download-btn" @click="downloadNow"></div>
        </div>
      </div>

      <div class="guide-open-browser" v-if="showGuideMask" @click="closeMask">
        <div class="guide-img"></div>
      </div>
    <!-- </pem-scroll-view> -->
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemIcon from 'components/pem-icon/pem-icon'
import { setTitle, device } from 'common/js/util'
import { MiniSiteApi } from 'api'

export default {
  components: {
    PemIcon,
    PemScrollView
  },
  data () {
    return {
      showGuideMask: false,
      isIOS: device.isIOS(),
      isWeixin: device.isWeixin(),
      isAndroid: device.isAndroid(),
      androidSchemeUrl: 'pemarket://www.pemarket.com.cn/attr?type=0&id=0',
      apkUrl: ''
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      setTitle('投融资信息对接服务平台')
      this.wxShare({})
      if (!this.isWeixin && this.isAndroid) {
        this.getAndroidApkUrl()
        window.location.href = this.androidSchemeUrl
      }
    },
    closeMask () {
      this.showGuideMask = false
    },
    openNow () {
      if (this.isIOS) {
        window.location.href = `https://go.pemarket.com.cn/download-guide?type=0&id=0`
      } else {
        if (this.isWeixin) {
          this.showGuideMask = true
        } else {
          window.location.href = this.androidSchemeUrl
          setTimeout(() => {
            window.location.href = this.apkUrl
          }, 2000)
        }
      }
    },
    downloadNow () {
      if (this.isIOS) {
        window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
      } else {
        window.location.href = this.isWeixin ? 'http://a.app.qq.com/o/simple.jsp?pkgname=com.pemv2' : this.apkUrl
      }
    },
    getAndroidApkUrl () {
      MiniSiteApi.getApkUrl().then(data => {
        this.apkUrl = (data && data.url) ? data.url : ''
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin"
  .download-lead
    position: fixed
    left: 0
    top: 0
    right: 0
    bottom: 0
    background: #ffffff
  .download-wrapper
    position: absolute
    left: 0
    top: 50%
    transform: translate(0, -50%) 
    width: 100%
    height: 9.52rem
    .logo
      width: 5.28rem
      height: 4.68rem
      margin: 0 auto
      bg-image('./images/logo')
      background-size: 100% 100%
    .intro
      width: 4.20rem
      height: 1.50rem
      margin: 0.30rem auto 0
      bg-image('./images/intro')
      background-size: 100% 100%
    .btns-wrapper
      position: absolute
      left: 50%
      bottom: 0
      width: 5.70rem
      transform: translate(-50%, 0) 
    .open-btn, .download-btn
      width: 2.60rem
      height: 0.72rem
      background-size: 100% 100%
    .open-btn
      float: left
      bg-image('./images/btn-open')
    .download-btn
      float: right
      bg-image('./images/btn-download')
  .guide-open-browser
    position: fixed
    left: 0
    top: 0
    right: 0
    bottom: 0
    z-index: 1000
    opacity: 0.7
    background: #000000
    .guide-img
      width: 5.12rem
      height: 2.54rem
      margin: 0.48rem auto 0
      bg-image('./images/bg_guide')
      background-size: 100% 100%
</style>



